import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
// import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { styled, alpha } from "@mui/material/styles";
import {
  Navbar,
  Nav,
  NavItem,
  NavDropdown,
  Container,
  Row,
  Col,
} from "react-bootstrap";

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(3)})`,
    transition: theme.transitions.create("width"),
    width: "100%",
  },
}));
export default function CustomAppBar() {
  const [age, setAge] = React.useState("");

  const handleChange = (event) => {
    setAge(event.target.value);
  };
  return (
    <AppBar position="static" color="transparent">
      <Container>
        <Toolbar>
          <Row>
            <Col md={12}>
              <div
                style={{
                  width: "100%",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <div style={{ display: "flex", alignItems: "center" }}>
                  <Typography variant="h6" noWrap component="div">
                    <Typography
                      sx={{
                        color: "#FF4D00",
                        fontSize: { xs: "20px", sm: "30px", md: "40px" },
                        fontFamily: "serif",
                        fontWeight: "bold",
                      }}
                      component="span"
                    >
                      Foxtel
                    </Typography>
                    <Typography
                      sx={{
                        color: "black",
                        fontSize: { xs: "20px", sm: "30px", md: "40px" },
                        fontFamily: "serif",
                        fontWeight: "bold",
                      }}
                      component="span"
                    >
                      PBX
                    </Typography>
                  </Typography>
                </div>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    marginLeft: "10%",
                  }}
                >
                  <Typography
                    sx={{
                      display: { xs: "none", sm: "block", md: "block" },
                      fontSize: "17px",
                      color: "rgb(50 50 50)",
                      letterSpacing: "0px",
                      paddingRight: "30px",
                    }}
                    noWrap
                    component="div"
                  >
                    Talk to an expert: 000-000-000
                  </Typography>
                  <Typography
                    sx={{
                      display: { xs: "none", sm: "block", md: "block" },
                      fontSize: "17px",
                      color: "rgb(50 50 50)",
                      letterSpacing: "0px",
                      paddingRight: "30px",
                    }}
                    noWrap
                    component="div"
                  >
                    Support
                  </Typography>
                  <FormControl
                    variant="standard"
                    sx={{
                      display: { xs: "none", sm: "none", md: "flex" },
                      m: 1,
                      minWidth: 100,
                    }}
                  >
                    <InputLabel id="demo-simple-select-label">Login</InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={age}
                      label="Login"
                      onChange={handleChange}
                    >
                      <MenuItem value={10}>Login</MenuItem>
                      <MenuItem value={20}>Twenty</MenuItem>
                      <MenuItem value={30}>Thirty</MenuItem>
                    </Select>
                  </FormControl>
                  <Search
                    sx={{
                      display: { xs: "none", sm: "none", md: "flex" },
                      alignItems: "center",
                    }}
                  >
                    <SearchIconWrapper sx={{ position: "relative" }}>
                      <SearchIcon />
                    </SearchIconWrapper>
                    <StyledInputBase
                      placeholder="Search…"
                      inputProps={{ "aria-label": "search" }}
                      size="small"
                      autoFocus={true}
                      color="primary"
                      style={{
                        backgroundColor: "#f8f9fa",
                        borderRadius: "5px",
                      }}
                    />
                  </Search>
                </div>
              </div>
            </Col>
            <Col md={12}>
              <Typography component="div">
                <Navbar expand="lg">
                  <Container>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                      <Nav className="me-auto">
                        <Nav.Link href="/home">Home</Nav.Link>
                        <Nav.Link href="/aboutus"> About Us</Nav.Link>
                        <Nav.Link href="/compatibility">Compatibility</Nav.Link>
                        <NavDropdown
                          title="Products and Services"
                          id="basic-nav-dropdown"
                        >
                          <NavDropdown.Item href="/product">
                            Products and Services
                          </NavDropdown.Item>

                          <NavDropdown.Item href="#action/3.3">
                            Pricing
                          </NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.4">
                            Full Account access
                          </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="/price"> Pricing</Nav.Link>
                        <NavDropdown
                          title=" Full Account access"
                          id="basic-nav-dropdown"
                        >
                          <NavDropdown.Item href="#action/3.1">
                            Products and Services
                          </NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.2">
                            Products and Services
                          </NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.3">
                            Pricing
                          </NavDropdown.Item>
                          <NavDropdown.Item href="#action/3.4">
                            Full Account access
                          </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#backbone">
                          Backbone Infrastructure
                        </Nav.Link>
                      </Nav>
                    </Navbar.Collapse>
                  </Container>
                </Navbar>
              </Typography>
            </Col>
          </Row>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
