import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { Container } from "react-bootstrap";
import { SocialIcon } from "react-social-icons";

export default function Footer() {
  return (
    <AppBar position="static" style={{ backgroundColor: "rgb(233, 233, 233)" }}>
      <Container>
        <Toolbar
          sx={{
            paddingTop: "20px",
            paddingBottom: "20px",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Typography component="div">
            <Typography
              component="span"
              sx={{
                color: "#767575",
                fontSize: { xs: "10px", sm: "12px", md: "15px" },
                fontFamily: "Roboto",
                borderRightWidth: "2px",
                borderRightStyle: "solid",
                paddingRight: "4px",
              }}
            >
              @2020 Foxtel
            </Typography>
            <Typography
              component="span"
              sx={{
                color: "#767575",
                fontSize: { xs: "10px", sm: "12px", md: "15px" },
                fontFamily: "Roboto",
                marginLeft: "4px",
              }}
            >
              All rights reserved
            </Typography>
          </Typography>
          <Typography component="div">
            <SocialIcon url="https://www.facebook.com/CreativeTim?ref=creativetim" />
            <SocialIcon
              url="https://twitter.com/CreativeTim?ref=creativetim"
              style={{ marginLeft: "10px" }}
            />
            <SocialIcon
              url="https://www.linkedin.com/CreativeTim?ref=creativetim"
              style={{ marginLeft: "10px" }}
            />
            <SocialIcon
              url="https://www.instagram.com/CreativeTimOfficial?ref=creativetim"
              style={{ marginLeft: "10px" }}
            />
          </Typography>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
